






import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import { EventBus } from '../eventbus';

@Component
export default class DDSearch extends Vue {
  private projSearch = "";

  @Prop({default: true})
  disabled !: boolean;

  @Watch('projSearch')
  onChange(val: string){
    this.$emit('searchEvt', this.projSearch)
  }

  private mounted(){
    EventBus.$on('clear', ()=>{
      this.projSearch ="";
    })
  }
}
